.app{
  background-color: #EFF0F6;
  background-repeat: repeat;
  min-height: 100vh;
}

.appWrap {
  display: grid;
  grid-template-areas: "h h h h h" ". n p . .";
  grid-template-rows: 70px 1fr;
  grid-template-columns: 3fr 170px 1fr 170px 3fr;
  font-family: "Open Sans", monospace;
  font-weight: normal;
  font-size: 1.1rem;
}

.appWrapContent  {
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  grid-area: p;
  background-color: white;
  min-width: 600px;
}

@media (max-width: 1000px) {
  .appWrap {
    grid-template-columns:20px 150px 1fr 20px;
  }
  .appWrapContent  {
    min-width: 0px;
  }

}

@media (max-width: 700px) {
  .appWrap {
    grid-template-areas: "h" "n" "p";
    grid-template-columns:1fr;
    grid-template-rows: 70px 150px 1fr;
  }
}









